/*样式重置*/
html,
body,
div,
ul,
li,
ol,
dl,
dd,
dt,
p,
h1,
h2,
h3,
h4,
h5,
h6,
form,
fieldset,
legend,
img {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  line-height: normal;
}
fieldset,
img {
  border: 0;
}
html,
body {
  background-color: #f5f5f5;
}
body {
  font-size: 0.3rem;
  font-family: MicrosoftYaHei;
}
img {
  width: 100%;
  /*解决图片底部对其问题*/
  display: block;
  border: none;
}
address,
caption,
cite,
code,
dfn,
th,
var {
  font-style: normal;
  font-weight: normal;
}
ul,
ol,
li {
  list-style: none;
}
input {
  padding: 0;
  margin: 0;
  font-family: "SimSun", "\5B8B\4F53";
}
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}
select,
input {
  vertical-align: middle;
}
select,
input,
textarea {
  font-size: 12px;
  margin: 0;
}
input[type="text"],
input[type="password"],
textarea {
  outline-style: none;
  -webkit-appearance: none;
}
textarea {
  resize: none;
}
table {
  border-collapse: collapse;
}
td,
th {
  padding: 0;
  margin: 0;
}
a {
  text-decoration: none;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  text-decoration: none;
  font-weight: normal;
  font-size: 100%;
}
textarea {
  resize: none;
}
i,
em {
  font-style: normal;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none !important;
}
input[type="number"] {
  -moz-appearance: textfield;
}
select {
  border: solid 1px #000;
  /*很关键：将默认的select选择框样式清除*/
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  /*在选择框的最右侧中间显示小箭头图片*/
  background: url("http://ourjs.github.io/static/2015/arrow.png") no-repeat right center transparent;
}
select::-ms-expand {
  display: none;
}
::-webkit-input-placeholder {
  /* WebKit, Blink, Edge */
  color: #999999;
}
:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #999999;
}
::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #999999;
}
:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #999999;
}
input {
  color: #000000;
}
.mint-indicator-wrapper {
  z-index: 10;
}
.mint-indicator {
  -webkit-transition: none;
  transition: none;
}
